@import "https://cdn.jsdelivr.net/npm/bulma@0.9.4/css/bulma-rtl.min.css";

.App .content {
	margin: 10px;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	gap: 20px;
}

.App .toggle-collapse-button {
	width: 100%;
	left: 0;
	bottom: 0;
	position: absolute;
}

@media (max-width: 620px) {
	.App .content {
		flex-direction: column;
	}
	.App .content .panel {
		width: auto;
	}
}